type IconType =
    "triangle"
    | "circle"
    | "arrowUp"
    | "upDown"
    | "box"
    | "hexa"
    | "cross"
    | "balloon"
    | "balloonSmall"
    | "circus"
    | "cake"
    | "party"
    | "bitbaloon"

type SVGProps = {
    stroke?: boolean
    color?: string | number | any
    width: number
    icon: IconType
    left: string
    top: string
    hiddenMobile?: boolean
}

const SVG = ({stroke = false, color = ``, width, icon, left, top, hiddenMobile = false}: SVGProps) => (
    <svg
        sx={{
            position: `absolute`,
            stroke: stroke ? `currentColor` : `none`,
            fill: stroke ? `none` : `currentColor`,
            display: hiddenMobile ? hidden : `block`,
            color,
            width,
            left,
            top,
        }}
        viewBox={icons[icon].viewBox}
    >
        {icons[icon].shape}
    </svg>
)

export default SVG
